html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video
  margin: 0
  padding: 0
  border: 0
  font-size: 100%
  font: inherit
  vertical-align: baseline

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section
  display: block

body
  line-height: 1

ol, ul
  list-style: none

blockquote, q
  quotes: none

blockquote
  &:before, &:after
    content: ''
    content: none

q
  &:before, &:after
    content: ''
    content: none

table
  border-collapse: collapse
  border-spacing: 0
    
@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&display=swap')

/* variables */

$font: "Manrope", sans-serif
$green: #114917
$light-green: #73AF0E
$pink: #FB6EA7
$brown: #6C4320
$orange: #F99605
$light-yellow: #FAF4D1
$white: #FFFFFF
$dark-orange: #CE7B02

/* general */
.padding
    padding: 0 100px
  
.text
  font-family: $font
  font-size: 24px
  line-height: 1.5
  font-weight: 600

.text_underline
  text-decoration: underline
  font-weight: 600

.section__heading
  color: $green
  font-size: 64px
  font-family: $font
  font-weight: 800

html
  scroll-behavior: smooth
  scroll-padding-top: 150px
  overflow-x: hidden

@media (max-width: 1000px)
  .padding 
    padding: 0 50px

@media (max-width: 850px)
  .section__heading
    font-size: 50px

@media (max-width: 600px)
  .section__heading
    font-size: 35px

@media (max-width: 550px)
  .padding 
    padding: 0 30px
  
@media ( max-width: 450px )
  .text
    font-size: 20px

/* header */
.nav
  width: 100%
  height: 100px
  display: flex
  justify-content: space-between
  align-items: center
  position: sticky
  top: 0
  z-index: 10000
  background-color: $green
  padding: 0 100px
  box-sizing: border-box
  .nav__logo
      width: 90px
      height: 90px
      background-image: url(../public/img/logo_no-bg.png)
      background-position: center
      background-size: contain
      background-repeat: no-repeat
  .nav__info
      display: flex
      height: 100px
      align-items: center
      ul
          width: 100%
          display: flex
          flex-direction: row
          justify-content: space-between
          li
              font-family: $font
              margin-left: 50px
              display: flex
              justify-content: center
              align-items: center
              a
                  color: $white
                  text-decoration: none
                  font-size: 24px
                  font-weight: 600

.language-toggle
  width: 75px
  height: 40px
  background-color: $light-yellow
  border-radius: 50px
  position: relative
  cursor: pointer
  display: flex
  align-items: center
  justify-content: space-between
  padding: 2px
  box-sizing: border-box
  transition: background-color 0.3s

.toggle-slider
  width: 35px
  height: 35px
  background-color: $brown
  border-radius: 50px
  display: flex
  align-items: center
  justify-content: center
  font-weight: bold
  color: $white
  position: absolute
  transition: all 0.3s ease

  &.english
    left: 4px

  &.ukrainian
    left: calc(100% - 50% - 2px)

.header
    width: 100%
    height: calc( 100vh - 100px )
    display: flex
    flex-wrap: wrap
    box-sizing: border-box
    background-color: $green
    color: $white       
    .header__info
        width: 100%
        height: calc( 100vh - 120px )
        display: flex
        align-items: center
        justify-content: space-between
        .header__text
          display: flex
          flex-direction: column
          justify-content: space-around
          width: 55%
          height: 65%
          .heading
            color: $white
            font-size: 90px
            font-family: $font
            font-weight: 700
    .header__slider
      width: 45%
      height: 80%
      display: flex
      justify-content: flex-end
      align-items: center

.nav__button
  display: block
  width: 170px
  height: 65px
  display: flex
  justify-content: center
  align-items: center
  background-color: $orange
  border-radius: 25px
  box-shadow: inset -4px -2px 6px rgba(0, 0, 0, 0.25)
  &:hover
    transition: .5s
    width: 170px
    height: 60px
    box-shadow: inset 4px 2px 6px rgba(0, 0, 0, 0.25)
    color: $white
    background-color: $orange
    padding: 0px
    font-size: 1.2em
    background-color: $dark-orange
    
.nav__link:hover
  padding: 15px
  background-color: $light-green
  color: $light-yellow
  transition: .5s
  border-radius: 25px

.swiper
  width: 400px
  height: 550px

.swiper-slide
  display: flex
  align-items: center
  justify-content: center
  border-radius: 25px
  box-sizing: border-box
  font-size: 22px
  font-weight: bold
  color: #fff
  background-size: cover
  background-position: center
  background-repeat: no-repeat

  &:nth-child(1n)
    background-image: url(../public/img/header/7.jpg)
    

  &:nth-child(2n)
    background-image: url(../public/img/header/1.jpg)

  &:nth-child(3n)
    background-image: url(../public/img/header/2.jpg)


  &:nth-child(4n)
    background-image: url(../public/img/header/3.jpg)


  &:nth-child(5n)
    background-image: url(../public/img/header/4.jpg)


  &:nth-child(6n)
    background-image: url(../public/img/header/5.jpg)

  &:nth-child(7n)
    background-image: url(../public/img/header/6.jpg)


  &:nth-child(8n)
    background-image: url(../public/img/header/8.jpg)


  &:nth-child(9n)
    background-image: url(../public/img/header/9.jpg)


  &:nth-child(10n)
    background-image: url(../public/img/header/10.jpg)

@media  (max-width: 1390px)
  .header .header__info .header__text .heading
    font-size: 70px

@media (max-width: 1300px) 
  .header .header__info .header__text
    height: 50%
    width: 50%
  .header .header__slider
    width: 35%
  
@media (max-width: 1200px)
  .swiper
    width: 350px
    height: 500px
  .header .header__info .header__text
    width: 55%

.navBurger
  display: none

@media (max-width: 1150px) 
  .swiper
    width: 300px
    height: 450px
  .header .header__info .header__text
    width: 60%
  .nav 
    display: none 
  .navBurger 
    width: 100% 
    height: 100px 
    display: flex 
    justify-content: space-between 
    align-items: center 
    position: fixed 
    top: 0 
    z-index: 10000 
    background-color: $green 
    padding: 0 100px 
    box-sizing: border-box 
    .burgerButton
      justify-self: flex-end
    .navBurger__logo
      width: 90px
      height: 90px
      background-image: url(../public/img/logo_no-bg.png)
      background-size: contain
      background-repeat: no-repeat
      background-position: center
    .burgerButton 
      font-size: 50px 
      background: none 
      border: none 
      color: $white 
      cursor: pointer
      display: flex
      align-items: center
      justify-content: flex-end 
      text-align: right
      width: 56px
      height: 63px
    .navBurger__items 
      width: 100% 
      height: calc( 100dvh - 100px )
      display: none 
      flex-direction: column 
      justify-content: space-around 
      align-items: center 
      position: fixed 
      top: 100px 
      left: 0 
      background-color: $green 
      z-index: 9999 
      &.show
        display: flex 
      li 
        width: 100% 
        display: flex 
        align-items: center 
        justify-content: center 
        .text
          color: $white 
          text-decoration: none 

.headerAdaptive
  display: none

@media ( max-width: 1100px )
  .header
    display: none
  .headerAdaptive
    width: 100%
    height: 100dvh
    background-color: $green
    box-sizing: border-box
    display: flex
    align-items: center
    position: relative
    .headerAdaptive__info
      padding: 0 100px
      height: auto
      z-index: 1000
      position: absolute
      display: flex
      flex-direction: column
      justify-content: space-around
      .text
        color: $white
        margin-top: 30px
      .heading
        color: $white
        font-size: 90px
        font-family: $font
        font-weight: 700
    .swiper
      width: 100%
      height: 100%
      border-radius: 0
      position: absolute
      top: 0
      left: 0
      z-index: 0
      .swiper-slide
        border-radius: 0
        filter: grayscale(30%) brightness(60%)

@media (max-width: 1000px) 
  .navBurger
    padding: 0 50px
  .headerAdaptive .headerAdaptive__info
    padding: 0 50px

@media  (max-width: 755px)
  .headerAdaptive .headerAdaptive__info .heading
    font-size: 60px

@media  (max-width: 600px)
  .headerAdaptive .headerAdaptive__info .heading
    font-size: 45px

@media (max-width: 550px) 
  .navBurger
    padding: 0 30px
  .headerAdaptive .headerAdaptive__info
    padding: 0 30px

  /* MASCOT is all*/
.scrollingText
  width: 100%
  height: 70px
  background-color: $light-yellow
  display: flex
  align-items: center
  font-size: 35px
  letter-spacing: 15px
  font-family: $font

  .marquee
    margin: 0 auto
    white-space: nowrap
    overflow: hidden
    display: flex
    gap: 0rem // Optional space between repeated spans

    span
      display: inline-block
      animation: marquee 20s linear infinite
      margin-right: 40px

@keyframes marquee
  0%
    transform: translateX(0)
  100%
    transform: translateX(-100%)


/* about */
.about
  width: 100%
  margin-top: 50px
  display: flex
  flex-wrap: wrap
  justify-content: space-between
  .about__text
    width: 45%
    .text
      margin-top: 50px
  .about__cards
    width: 50%
    display: flex
    flex-wrap: wrap
    justify-content: space-between
    grid-row-gap: 20px
    .about__element
      display: flex
      flex-direction: column
      justify-content: space-around
      align-items: center
      width: 50%
      div
        width: 165px
        height: 165px
        border-radius: 50%
        background-color: $light-green
      a
        color: #000

@media (max-width: 1200px)
  .about .about__text
    width: 100%
  .about .about__cards
    width: 100%
    display: flex
    justify-content: space-between
    margin-top: 50px

@media (max-width: 850px)
  .about .about__cards .swiper
    width: 100%
    height: auto
    padding-bottom: 10px
    box-sizing: content-box
    .swiper-pagination
      position: relative
      padding-bottom: 20px
    .swiper-slide
      height: auto
      background: none
      .about__element
        display: flex
        flex-direction: column
        align-items: center
        justify-content: flex-start
        width: 100%
        height: 100%
        color: #000
        .text
          width: 100%
          text-align: center
          margin-top: 30px
  
  .swiper-pagination-bullet 
    width: 40px
    height: 10px
    border-radius: 10px
    background-color: $light-yellow
    opacity: 1
    transition: background-color 0.3s
    margin-top: 50px
  .swiper-pagination-bullet-active 
    background-color: $light-green

/* values */
.values
  margin-top: 80px
  width: 100%
  display: flex
  flex-wrap: wrap
  .section__heading_center
    text-align: center
    width: 100%
    display: inline-block
  .values__container
    width: 100%
    display: flex
    flex-wrap: wrap
    justify-content: space-around
    div
      width: 27%
      height: 350px
      margin-top: 50px
      border-radius: 25px
      border: 2px solid $brown
      box-sizing: border-box
      display: flex
      flex-direction: column
      align-items: center
      justify-content: space-around
      transition: .5s
      padding: 30px
      .values__info
        text-align: center
        display: none

.values .swiper
  width: 100%
  display: flex
  justify-content: space-around
  padding-bottom: 20px
  .swiper-slide
    color: #000
    background: none
    display: flex
    flex-direction: column
    align-items: center
    .value-block
      width: 80%
      height: 80%
      border-radius: 25px
      border: 3px solid $brown
      display: flex
      flex-direction: column
      justify-content: center
      align-items: center
      padding: 20px
      box-sizing: border-box
      .img
        border-radius: 25px
      .text
        text-align: center
        margin-top: 30px

@media (max-width: 1200px)
  .values .values__container div
    width: 40%

/* participants */
.participants
  margin-top: 50px
  width: 100%
  display: flex
  flex-wrap: wrap
  justify-content: space-between
  .swiper2
    display: none
  .swiper
    width: 30%
    height: 300px 
    margin: 0
    .swiper-slide
      border-radius: 25px
      background-size: contain
      background-repeat: no-repeat
      background-position: center
  .participants__info
    width: 65%
    display: flex
    flex-wrap: wrap
    align-items: center
    justify-content: flex-start
    .participants__bullet
      width: 43%
      height: 100px
      display: flex
      align-items: center
      justify-content: space-between
      flex-wrap: wrap
      &:nth-child(2n)
        margin-left: 30px
      &:nth-child(3n)
        margin-left: 50px
      &:nth-child(4n)
        margin-left: 30px
      div
        width: 80px
        height: 80px
        background-color: $light-yellow
        border-radius: 50%
      .text
        display: inline-block
        display: flex
        align-items: center
        width: calc( 100% - 40px )
        position: relative
        top: -50px
        left: 40px

@media (max-width: 1300px)
  .participants .participants__info
    width: 60%
    justify-content: space-between
  .participants .participants__info .participants__bullet:nth-child(3n), .participants .participants__info .participants__bullet:nth-child(4n), .participants .participants__info .participants__bullet:nth-child(2n)
    margin-left: 0
  .participants .participants__info .participants__bullet
    width: 48%
  .participants .swiper
    width: 35%

@media (max-width: 1200px)
  .participants .participants__info
    width: 100%
    justify-content: flex-start
    .participants__bullet
      width: 40%
      &:nth-child(3n)
        margin-left: 20%
  .participants .swiper
    display: none
  .participants .swiper2
      display: block
      width: 100%
      height: 300px
      margin-top: 50px

@media (max-width: 850px)
  .participants .participants__info .participants__bullet
    width: 45%
    &:nth-child(3n)
      margin-left: 10%
  
@media (max-width: 700px) 
  .participants .participants__info .participants__bullet
    width: 50%
    &:nth-child(3n)
      margin-left: 0
    .text
      height: auto

@media (max-width: 650px)
  .participants .participants__info .participants__bullet
    width: 100%
      
        
/* accomodations */
.padding_accomodations
  background-color: $green

.accomodations
  margin-top: 50px
  color: $white
  display: flex
  flex-wrap: wrap
  justify-content: space-between
  padding-bottom: 50px
  .swiper1participants
    display: flex
    justify-self: flex-end
    align-self: center
  .accomodations__bullets
    width: 40%
    margin-top: 50px
    div
      width: 100%
      display: flex
      margin-top: 30px
      &:first-child
        margin-top: 0
      .bullet
        width: 80px
        height: 80px
        border-radius: 50%
        background-color: $light-green
      .text
        width: calc( 100% - 80px )
        margin-left: -40px
        margin-top: 30px

.accomodations .swiper2participants
  display: none

@media ( max-width: 1300px ) 
  .accomodations .accomodations__bullets
    width: 50%
  .accomodations .swiper
    width: 40%

@media ( max-width: 1050px )
  .accomodations .swiper1participants
    display: none
  .accomodations .swiper2participants
    display: block
    width: 100%
    height: 300px
    margin-top: 50px
  .accomodations .accomodations__bullets
    width: 100%
    display: flex
    justify-content: space-between
    flex-wrap: wrap
    div
      width: 48%
      margin-top: 0
      &:nth-child(3n)
        margin-top: 50px
      &:nth-child(4n)
        margin-top: 50px

@media (max-width: 850px)
  .accomodations .accomodations__bullets div
    width: 100%
    margin-top: 30px
    &:nth-child(3n)
      margin-top: 30px
    &:nth-child(4n)
      margin-top: 30px
    
/* team */
.team
  margin-top: 50px
  width: 100%
  display: flex
  flex-wrap: wrap
  .team__info
    width: 100%
    display: grid
    grid-template-columns: repeat(4, 1fr)
    margin-top: 50px
    .team__slides
      width: 100%
      display: flex
      flex-direction: column
      align-items: center
      justify-content: space-between
      .team__photo
        width: 80%
        padding-bottom: 80%
        border-radius: 50%
        background-image: url(../public/img/placeholder.png)
        background-size: cover
        background-position: center
        background-repeat: no-repeat
        border: 3px solid $brown
        box-sizing: border-box
      .text
        margin-top: 30px

/* feedback */
.feedback
  margin-top: 50px
  width: 100%
  display: flex
  flex-wrap: wrap
  .swiper
    width: 100%
    margin-top: 50px
    height: auto
    .swiper-button-prev
      &:nth-child(1n)
        display: none
    .swiper-button-next
      color: $light-green
      font-weight: bold
      display: none
    .swiper-slide
      width: 80%
      background: none
      
.feedback
  text-align: center

.slide-content
  background: #fff
  padding: 20px
  border-radius: 10px
  text-align: left
  font-family: $font
  margin-bottom: 30px

.profile
  display: flex
  align-items: center
  justify-content: space-between
  margin-bottom: 10px
  color: #000
  font-weight: normal

.profile-image
  width: 50px
  height: 50px
  border-radius: 50%
  border: 2px solid $brown
  display: flex
  align-items: center
    

.profile-info
  display: flex
  flex-direction: column
  margin-left: 10px
  width: calc( 100% - 80px )

.profile-name
  font-weight: normal
  font-size: 24px

.profile-details
  font-size: 24px
  font-family: $font
  font-weight: normal

.slide-text
  font-size: 24px
  color: #000
  font-weight: normal
  margin-top: 20px
  line-height: 1.5

.swiper-pagination-bullet
  width: 85px
  height: 15px
  background-color: $light-yellow
  border-radius: 25px

.swiper-pagination-bullet-active
  background-color: $light-green

.swiper-pagination-bullet.custom-bullet
  width: 85px
  height: 15px
  background-color: $light-yellow
  border-radius: 15px
  opacity: 100
  margin: 0 7.5px

.swiper-pagination-bullet-active.custom-bullet
  background-color: $light-green
  border-radius: 15px

@media (max-width: 650px)
  .swiper-pagination-bullet.custom-bullet
    width: 50px

@media (max-width: 450px)
  .swiper-pagination-bullet.custom-bullet
    width: 35px

/* partners */
.partners 
    margin-top: 50px 
    display: flex 
    flex-wrap: wrap 
    .partners__slider 
        margin-top: 50px 
        display: flex 
        justify-content: flex-start 
        align-items: center 
        width: 100% 
        overflow: hidden 
        border: 1px solid rgba(255, 255, 255, 0.4) 
        position: relative 
        .slider__items 
            display: flex 
            justify-content: center 
            align-items: center 
            gap: 50px 
            animation: scrolling 30s linear infinite 
            width: max-content 
            margin-right: 50px
            a 
              display: block 
              width: 150px 
              height: 150px 
              background-size: contain 
              background-repeat: no-repeat 
              background-position: center 
              &:nth-child(1) 
                background-image: url(../public/img/partners/brandme.png) 
              &:nth-child(2) 
                background-image: url(../public/img/partners/dodo.png) 
              &:nth-child(3) 
                background-image: url(../public/img/partners/Golosy-ditej-fond.jpg) 
                background-size: 200% 
              &:nth-child(4) 
                background-image: url(../public/img/partners/klei.png) 
              &:nth-child(5) 
                background-image: url(../public/img/partners/morshynska.jpg) 
              &:nth-child(6) 
                background-image: url(../public/img/partners/orner.png) 
                background-size: contain 
              &:nth-child(7) 
                background-image: url(../public/img/partners/zone3000.png) 
@keyframes scrolling 
    0% 
      transform: translateX(0) 
    100% 
      transform: translateX(-50%) 

  
/* faq */
.faq
  margin-top: 50px
  .faq__questions
    width: 100%
    display: flex
    flex-wrap: wrap
    .accordion
      margin-top: 20px


.accordion 
    display: block
    background-color: $white
    border-radius: 25px
    cursor: pointer
    padding: 20px 30px
    width: 100%
    border: none
    text-align: left
    outline: none
    transition: 0.2s
    border: 2px solid $brown

.accordion:hover 
    background-color: $light-yellow

.active
    box-shadow: none

.panel 
    padding: 0 20px
    background-color: white
    max-height: 0
    overflow: hidden
    transition: max-height 0.2s ease-out
    .text
        padding: 25px 5px
/* footer */
.footer__padding
  background-color: $brown

.footer
  margin-top: 50px
  display: flex
  flex-wrap: wrap
  align-items: center
  padding: 30px 0
  justify-content: space-between
  .footer__copyright
    width: 33%
  .footer__logo
    width: 100px
    height: 100px
    background-image: url(../public/img/logo_bg.png)
    background-position: center
    background-size: contain
    background-repeat: no-repeat
  .footer_socials
    width: 33%
    display: flex
    align-items: center
    justify-content: flex-end
    a 
      display: block
      width: 40px
      height: 40px
      margin-left: 20px
      background-size: contain
      background-position: center
      background-repeat: no-repeat
      &:nth-child(1n)
        background-image: url(../public/img/logos/instagram.png)
      &:nth-child(2n)
        background-image: url(../public/img/logos/youtube.png)
